
.setup {
	width: 100vw;
	/*height: 100vh;*/
	background: rgba(255, 255, 255, 0.85);
	position: absolute;
	left: 0;
	top: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
}
.setup > .wrapper {
	width: 85vw;
  text-align: center;
}
.copy-phrase-button{
  margin-top: 20px;
  margin-bottom: 44px;
}

/*#appframe {
  width: 50vw;
  height: 50vh;
}*/
.seed {
	font-style: italic;
}
.seed-odd {
	font-weight: 700;
}
.seed-input {
  font-family: 'Roboto Mono', monospace;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.33;
  text-align: center;
  color: #1e1e1e;
  min-height: 90px;
  border-radius: 10px;
  border: solid 2px #f5f5f5;
  padding: 15px 24px 0;
  width: 100%;
  resize: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.title{
  text-align: center;
}
.active-addr {
	font-weight: 700;
}
.next-step-button{
  margin-top: 40px;
}
.save-password-button{
  margin-top: 135px;
}
.pin-input-label{
  display: inline-block;
  margin-top: 40px;
}
.password-info:empty{
  display: none;
}



